.footer-container {
    background-color: #000;
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.footer-introduce {
    color: #fff;
    margin: 0;
    font-size: 1.2rem;
}
.footer-society {
    display: flex;
    align-items: center;
    justify-content: center;
}
.footer-society__logo {
    width: 40px;
    margin-right: 6px;
    border-radius: 50px;
}
